@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-size: 16px;  /*     */
  }
  
  body {
    @apply text-base;  /*  text-lg    */
  }
}

/*     */
.container {
  @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;  /*     */
}

/*       */
.card {
  @apply w-full;  /*    */
}

/*      */
.section {
  @apply py-8 md:py-12;  /*    */
}

/*     */
.layout-container {
  @apply min-h-screen w-full;
}
